.about {
  position: relative;
  padding: 114px 0 96px;
  overflow: hidden;
  max-width: 1440px;
  margin: 0 auto;

  .container {
    @include vp-1229 {
      width: 94%;
    }

    @include vp-1199 {
      width: 100%;
    }
  }

  &::before {
    position: absolute;
    content: "";
    top: 97px;
    left: 0;
    width: 808px;
    height: 474px;
    /* stylelint-disable */
    background-image: -webkit-image-set(
      url("../img/about/hike.webp") type("image/webp"),
      url("../img/about/hike.png") type("image/png"));
    background-image: image-set(
      url("../img/about/hike.webp") type("image/webp"),
      url("../img/about/hike.png") type("image/png"));
    /* stylelint-enable */
    background-size: cover;
  }

  &::after {
    position: absolute;
    content: "";
    top: -66px;
    right: -16%;
    width: 840px;
    height: 280px;
    /* stylelint-disable */
    background-image: -webkit-image-set(
      url("../img/about/clouds.webp") type("image/webp"),
      url("../img/about/clouds.png") type("image/png"));
    background-image: image-set(
      url("../img/about/clouds.webp") type("image/webp"),
      url("../img/about/clouds.png") type("image/png"));
    /* stylelint-enable */
    background-repeat: no-repeat;
  }

  @media (min-width: 1200px) {
    @include retina {
      &::before {
        background-image: url("../img/about/hike@2x.png");
        background-size: cover;
      }
    }
  }

  h2 {
    margin-bottom: 56px;
    padding-top: 17px;
  }

  h3 {
    font-size: 21px;
    font-weight: 700;
    line-height: 100%;
    margin: 0 0 16px;
  }

  p {
    font-size: 14px;
    line-height: 157%;
    margin: 0 0 30px;

    &:last-of-type {
      margin-bottom: 0;
    }
  }

  @include vp-1199 {
    padding: 63px 0 77px;

    &::before {
      top: 81px;
      width: 685px;
      height: 402px;
    /* stylelint-disable */
    background-image: -webkit-image-set(
      url("../img/about/hike-tablet.webp") type("image/webp"),
      url("../img/about/hike-tablet.png") type("image/png"));
    background-image: image-set(
      url("../img/about/hike-tablet.webp") type("image/webp"),
      url("../img/about/hike-tablet.png") type("image/png"));
    /* stylelint-enable */
      background-repeat: no-repeat;
      background-size: contain;
    }

    &::after {
      display: none;
    }

    br {
      display: none;
    }

    h2 {
      margin-bottom: 35px;
      padding-top: 11px;
    }

    h3 {
      font-size: 18px;
    }

    p {
      margin-bottom: 28px;
    }
  }

  @include vp-767 {
    padding: 16px 0 61px;

    &::before {
      position: static;
      display: block;
      margin-bottom: 44px;
    /* stylelint-disable */
    background-image: -webkit-image-set(
      url("../img/about/hike-mobile.webp") type("image/webp"),
      url("../img/about/hike-mobile.png") type("image/png"));
    background-image: image-set(
      url("../img/about/hike-mobile.webp") type("image/webp"),
      url("../img/about/hike-mobile.png") type("image/png"));
    /* stylelint-enable */
    }

    @include retina {
      &::before {
        background-image: url("../img/about/hike-mobile@2x.png");
        background-size: contain;
      }
    }

    h2 {
      margin-bottom: 32px;
      padding-top: 3px;
    }

    h3 {
      font-size: 16px;
      margin-bottom: 18px;
    }

    p {
      line-height: 143%;
      margin-bottom: 41px;
    }
  }
}

.about__wrapper {
  position: relative;
  width: 49%;
  margin-left: auto;

  @include vp-1199 {
    width: 48%;
  }

  @include vp-767 {
    width: 100%;
  }
}


.about__content {
  display: grid;
  margin: 0;
  padding: 0;
  list-style: none;
}
